import React from "react";
import { ButtonProps, Styles } from "../interfaces";
import { hexColorToRGB } from "../utils";
import "./JButton.scss";
const buttonStyles = (style: Styles | undefined): React.CSSProperties => {
  let bg_color = undefined;
  if (style?.backgroundColor) {
    bg_color = style.backgroundColor;
  }
  return {
    minWidth: "64px",
    padding: "6px 16px",
    borderRadius: "5px",
    // border: "none",
    //color: "white",
    textAlign: "center",
    textDecoration: "none",
    lineHeight: "1.75",
    display: "flex",
    fontSize: "0.875rem",
    margin: "1%",
    transition: `background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms`,
    //transition-duration: 0.4s;
    cursor: "pointer",
    fontWeight: 500,
    backgroundColor: `${
      style?.backgroundColor
        ? hexColorToRGB(style.backgroundColor, 0.8)
        : "rgba(255, 255, 255, 0.8)"
    }`,
    color: style?.themeColor || "#ff6c37",
    border: `1.5px solid ${style?.themeColor || "#ff6c37"}`,
    textTransform: "uppercase",
  };
};
const JButton: React.FC<ButtonProps> = (props: ButtonProps) => {
  const { children, onClick, style } = props;
  const handleClick = () => {
    if (onClick) onClick();
  };

  return (
    <button
      className="Jbutton-outlined"
      style={buttonStyles(style)}
      onClick={handleClick}
    >
      {children}
    </button>
  );
};

export default JButton;
