import React, { useEffect, useRef } from "react";
import JButton from "./JButton/JButton.js";
import "./DropZone.scss";
import {
  DropZoneProps,
  ErrorsValidation,
  Labels,
  ResultFileValidation,
  Styles,
  TextStyles,
} from "./interfaces.js";
import { hexColorToRGB } from "./utils.js";
const makeLabels = (localization: string): Labels => {
  switch (localization) {
    case "es-ES":
      return {
        mainLabel: "Suelta tus archivos aquí",
        mainLabel_or: "o",
        bottonLabel: "Extensiones permitidas:",
        buttonLabel: "Busca tus archivos...",
      };
      case "en-EN":
      return {
        mainLabel: "Drop your file here",
        mainLabel_or: "or",
        bottonLabel: "Extensions allowed:",
        buttonLabel: "Select your files...",
      };
    default:
      //  "en-EN"
      return {
        mainLabel: "Drop your file here",
        mainLabel_or: "or",
        bottonLabel: "Extensions allowed:",
        buttonLabel: "Select your files...",
      };
  }
};
const makeErrors = (localization?: string): ErrorsValidation => {
  switch (localization) {
    case "es-ES":
      return {
        mime: "Tipo de archivo no permitido",
        ext: "Extensión de archivo no permitida",
        size: "Tamaño maximo exedido",
      };
    default:
      //  "en-EN"
      return {
        mime: "Mime type not allowed",
        ext: "File extension not allowed",
        size: "Max file size exceeded",
      };
  }
};

const makeDropZoneStyles = (style: Styles | undefined): React.CSSProperties => {
  return {
    border: `2px dashed ${style?.themeColor || "#ff6c37"}`,
    borderRadius: "2%",
    backgroundImage: `linear-gradient(
      to top,
      ${style?.backgroundColor?hexColorToRGB(style.backgroundColor,0.6):"rgba(255, 255, 255, 0.6)"},
      ${style?.backgroundColor?hexColorToRGB(style.backgroundColor,0.6):"rgba(255, 255, 255, 0.6)"}
    ),
    url(${
      style?.backgroundImage ||
      "https://www.postman.com/assets/use-cases-by-role.svg"
    })`,
    width: "100%",
    padding: "1%",

    // "background-size": "inherit",
  };
};
const makeTextStyles = (style: TextStyles | undefined): React.CSSProperties => {
  if(!style){
    return undefined;
  }
  return {
    fontFamily: style.fontFamily,
    color: style.color,
    fontSize: style.fontSize,
  };
};
const DropZone: React.FC<DropZoneProps> = (props?: DropZoneProps) => {
  const { style, limits, amountOfFiles, localization } = props;
  const input_ref = useRef(null);
  const dz_ref = useRef(null);
  const handleDragOver = (evt) => {
    evt.stopPropagation();
    evt.preventDefault();
    evt.dataTransfer.dropEffect = "link"; // Explicitly show this is a copy.
  };
  const getExt = (fileName: string): string | undefined => {
    const re = /(?:\.([^.]+))?$/;
    return re.exec(fileName)[1];
  };

  const resultOfValidation = (
    fileList: Array<File>,
  ): Array<ResultFileValidation> => {
    const errorsEncountered = makeErrors(localization);
    let result: Array<ResultFileValidation> = [];
    fileList.forEach((f: File) => {
      result.push({ file: f, errors: [] });
    });

    if (limits) {
      result.forEach((res) => {
        const { file } = res;
        //console.log("type: ", file.type);
        if (limits.mimeType) {
          if (!limits.mimeType.includes(file.type)) {
            res.errors = [...res.errors, errorsEncountered.mime];
          }
        }

        const ext = getExt(file.name) || "";
        if (limits.extensions) {
          if (!limits.extensions.includes(ext)) {
            res.errors = [...res.errors, errorsEncountered.ext];
          }
        }
        if (limits.maxSize) {
          if (limits.maxSize > file.size * 1024 * 1024) {
            res.errors = [...res.errors, errorsEncountered.size];
          }
        }
      });
    }

    return result;
  };
  const handleFileSelect = async (evt): Promise<void> => {
    evt.stopPropagation();
    evt.preventDefault();

    let maxAmountOfFiles: number = amountOfFiles || 10;
    maxAmountOfFiles = maxAmountOfFiles <= 0 ? 1 : maxAmountOfFiles;
    let files: Array<File> = [];
    if (!evt.dataTransfer) {
      //se clickeo al boton
      //console.log("Caraga por input");
      files = evt.target.files;
      //console.log("primer file", evt.target.files[0]);
    } else {
      //se uso el DropZone
      // console.log("Caraga por dropZone", evt.dataTransfer.files);
      files = evt.dataTransfer.files;
    }
    let output = [];

    if (maxAmountOfFiles) {
      //en caso haya limite
      const fileListSize = files.length;
      for (let i = 0; i < Math.min(maxAmountOfFiles, fileListSize); i++) {
        output.push(files[i]);
      }
    } else {
      //si no hay limite
      for (let i = 0, f: File; (f = files[i]); i++) {
        output.push(f);
      }
    }

    const finalList: Array<ResultFileValidation> = resultOfValidation(output);
    if (props.handleFileSelect) props.handleFileSelect(finalList);
  };
  const dz_handleClick = (): void => {
    //alert("click");
    input_ref.current.click();
  };

  const init = async (): Promise<void> => {
    //console.log("init");
    dz_ref.current.addEventListener("dragover", handleDragOver, false);
    dz_ref.current.addEventListener("drop", handleFileSelect, false);
  };
  const renderListOfExtensions = (listExt: Array<string>|undefined): string => {
    if (!listExt) {
      return "";
    }
    const size = listExt.length;
    switch (size) {
      case 0:
        return "";
      case 1:
        return "" + listExt[0];
      default: {
        let listExtensions = " ." + listExt[0];
        for (let i = 1; i < listExt.length; i++) {
          listExtensions += ", ." + listExt[i];
        }
        return listExtensions;
      }
    }
  };
  const listOfExtensionsShow: string = renderListOfExtensions(
    limits?.extensions
  );
  const _labels:Labels = localization?makeLabels(localization):makeLabels("en-EN");
  useEffect(() => {
    init();
  });

  return (
    <div>
      <div className="dz-container" style={{backgroundColor:(style?.backgroundColor?style.backgroundColor:"white")}}>
        <div className="dz" ref={dz_ref} style={makeDropZoneStyles(style)}>
          <div className="dz-content">
            <h1 style={makeTextStyles(style?.mainTextStyle)}>
              {`${_labels.mainLabel}`}
            </h1>
            <h1 style={makeTextStyles(style?.mainTextStyle)}>
              {`${_labels.mainLabel_or}`}
            </h1>
            <JButton style={style || undefined} onClick={dz_handleClick}>
              {`${_labels.buttonLabel}`}
            </JButton>
            {listOfExtensionsShow && listOfExtensionsShow.length > 0 ? (
              <h2 style={makeTextStyles(style?.bottonTextStyle)}>
                {`${_labels.bottonLabel}`}
                <strong>{" " + listOfExtensionsShow}</strong>
              </h2>
            ) : (
              <></>
            )}
          </div>
        </div>
      </div>
      <input
        ref={input_ref}
        type="file"
        id="inputSSJ"
        style={{ display: "none" }}
        onChange={handleFileSelect}
        multiple
      />
    </div>
  );
};

export default DropZone;
